body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@font-face {
  font-family: "Montserrat";
  src: url(/static/media/Montserrat-Regular.91696e09.otf);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
}

strong {
  font-weight: 550;
}

section {
  padding: 1rem 0;
}

section#contact {
  padding: 3.5rem 0;
}

section.footer {
  padding-top: 3.5rem;
  padding-bottom: 0rem;
}

nav {
  background-color: white;
}

.nav-text-name,
.brand-logo.center {
  color: #607d8b;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
  font-weight: bold;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}

.side-nav {
  position: absolute;
  width: 40vw;
  left: 115%;
  background-color: white;
  height: 100vh;
  outline-color: white;
}

.side-nav li > a:hover {
  background-color: #607d8b;
}

.nav-text-name:hover,
.black-color.sideNavClose.left {
  color: black;
}

.nav-text {
  color: black;
  text-transform: uppercase;
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
  font-weight: bold;
}

.nav-text:hover,
.nav-text:active {
  background-color: #607d8b;
  color: white;
}

.left.material-icons.sideNavClose {
  margin: 0;
  padding-right: 0;
}

.button-collapse > .material-icons {
  color: black;
  margin: 0;
}

nav .button-collapse {
  float: right;
  cursor: pointer;
}

.landing-img > img {
  width: 100%;
}

hr {
  max-width: 150px;
  height: 8px;
  border-radius: 2px;
  background-color: #607d8b;
  margin-top: 3%;
  margin-bottom: 3%;
}

h3.white-color,
h4.white-color,
.white-color,
.white-color.sideNavClose.left {
  color: white;
}

hr.white-color {
  background-color: white;
}

hr.sub {
  max-width: 50px;
  height: 5px;
  border-radius: 1px;
  margin-top: 2%;
  margin-bottom: 2%;
}

.text-faded {
  font-size: 1.35rem;
}

.card-image > img {
  width: 100%;
}

.row .col.card {
  padding: 0px 4px;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.2);
  margin-top: 0.5%;
  margin-bottom: 0;
}

.card .card-title {
  font-weight: bold;
}

.btn,
.btn:focus {
  background-color: #607d8b;
  font-weight: bold;
  padding: 0 1rem;
  margin-right: 5%;
}

.btn:hover {
  background-color: #607d8b;
  -webkit-filter: brightness(125%);
          filter: brightness(125%);
}

.work-img {
  object-fit: cover;
}

#minust {
  background-color: #607d8b;
}

.background-info {
  display: inline-block;
}

#aboutImg {
  -webkit-filter: drop-shadow(10px 8px 2px #333333);
          filter: drop-shadow(10px 8px 2px #333333);
}

.bgImg {
  display: block;
  width: 40%;
  margin: 3% auto;
  -webkit-filter: drop-shadow(5px 5px 2px #333333);
          filter: drop-shadow(5px 5px 2px #333333);
}

.background-text {
  padding: 5%;
}

p.text-faded {
  margin-top: 5%;
  margin-bottom: 5%;
}

/* .millegaTegelen > img {
  width: 50%;
} */

.material-icons.prefix.active {
  color: #607d8b;
}

p.text-faded.contact {
  margin: 2%;
  text-align: center;
}

p.text-faded.contact.dark {
  color: black;
}

form > .row {
  margin-bottom: 0.5%;
}

.contact-response {
  display: inline-block;
}

.contact-response.preloader-wrapper {
  width: 25px;
  height: 25px;
}

.minuPilt {
  margin: auto;
}

#videod {
  box-shadow: 30px 0px 40px rgba(0, 0, 0, 0.1),
    -30px 0px 40px rgba(0, 0, 0, 0.1);
}

#videod .video-container {
  margin-top: 5%;
}

.footer-links {
  display: inline-block;
  text-align: center;
  margin-left: 3%;
  margin-right: 3%;
}

.row.footerRow {
  text-align: center;
}

#github-logo,
#linkedin-logo {
  width: 50px;
}

.footer-links .material-icons,
.footer-links > a {
  color: #000;
  font-weight: 500;
}

.footer-links > a:hover {
  text-decoration: underline;
}

.footer-links i {
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

.footer-links i:active {
  -webkit-transform: translateY(7px);
          transform: translateY(7px);
}

.footer-links img:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

@media (max-width: 991px) {
  .skillsText {
    margin-top: 2%;
  }
  /* .minuPilt {
    width: 90%;
  } */
}

.video-container {
  position: relative;
}

video {
  width: 100%;
  /* Could also use width: 100%; */
  height: 100vh;
  object-fit: cover;
  /* position: fixed; */
  /* Change position to absolute if you don't want it to take up the whole page */
  left: 0px;
  top: 0px;
  z-index: -1;
}

.overlay-desc {
  background: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  right: 10;
  bottom: 40;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.overlay-desc h1 {
  color: white;
  font-family: "Nobile", sans-serif;
  font-size: 9vw;
  text-align: center;
}

.overlay-desc img {
  width: 100%;
}

.portfolioKorgus {
  height: 94vh;
}

.hide-true {
  display: none;
}

.hide-false {
  display: block;
}

.tekst-valge {
  color: white!important;
}

.modalPilt{
  margin-top: 1%;
  margin-bottom: 1%;
}


